

*{
    margin: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
}
body{
    background-image: url(./images/night.png);
}

.weather--main{
    width: 300px;
    height: 300px;
    /* border: 1px solid; */
    margin: auto;
    border-radius: 50%;
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.weather--search{
    padding: 10px 20px;
    border-radius: 30px;
    border: none;
    background-color: antiquewhite;
    text-align: center;
    text-transform: capitalize;
}
.weather--city{
    padding: 10px;
    text-transform: capitalize;
}
.weather--desc{
    padding: 10px;
}
.weather--loaction{
    display: flex;
}
.weather--location_icon{
    width: 40px;
    filter: brightness(30%);
}
.weather--earth{
    position: absolute;
    width: 300px;
    opacity: .75;
    border-radius: 50%;
}
.weather--noData{
    padding: 10px;
}
.weather--position{
    position: relative;
}